.message,
.form,
form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

body {
    background: #ebebeb;
    height: 100%;
    font-family: "Source Sans Pro", sans-serif;
}

.sign--container {
    height: 400px;
    background: #f2eee3;
    position: relative;
    grid-template: 100%/50% 50%;
    box-shadow: 2px 2px 10px 0 rgba(51, 51, 51, 0.2);
}

.message {
    position: absolute;
    background-color: #f2eee3;
    width: 50%;
    height: 100%;
    transition: 0.5s all ease;
    z-index: 4;
}
.message:before {
    position: absolute;
    content: "";
    width: 1px;
    height: 70%;
    background: #c3c3d8;
    opacity: 0;
    left: 0;
    top: 15%;
}
.message .button {
    margin: 5px;
}

.signup:before {
    opacity: 0.3;
    left: 0;
}

.login:before {
    opacity: 0.3;
    left: 100%;
}

.btn-wrapper {
    width: 60%;
}

.form {
    width: 100%;
    height: 100%;
}
.form--heading {
    height: 50px;
    color: #8288ff;
    font-family: "Source Sans Pro", sans-serif;
}
.form--signup {
    border-right: 1px solid #999;
}

form {
    width: 70%;
}
form > * {
    margin: 10px;
}
form input {
    width: 90%;
    border: 0;
    border-bottom: 1px solid #aaa;
    font-size: 13px;
    font-weight: 300;
    color: #797a9e;
    letter-spacing: 0.11em;
    background-color: #f2eee3;
}
form input::placeholder {
    color: #333;
    font-size: 10px;
}
form input:focus {
    outline: 0;
    border-bottom: 1px solid rgba(128, 155, 206, 0.7);
    transition: 0.6s all ease;
}

.button {
    width: 100%;
    height: 30px;
    border: 0;
    outline: 0;
    color: #f2eee3;
    font-size: 15px;
    font-weight: 400;
    position: relative;
    z-index: 3;
    background: #8288ff;
    font-family: "Source Sans Pro", sans-serif;
    cursor: pointer;
    border-radius: 5px;
}
